<template>
    <div>
        <b-form @submit="onSubmit" @reset="onReset">
            <b-form-group id="inputGroup1"
                          label="Search Lotto Number"
                          label-for="input1"
                          description="조회하려는 로또 회차를 입력하여주시기 바랍니다.">
                <b-form-input id="input1"
                              type="number"
                              v-model="form.drawNo"
                              required
                              placeholder="Enter Draw Number">
                </b-form-input>
            </b-form-group>
            <b-button type="submit" variant="primary">조회</b-button>
        </b-form>

        <div v-if="showResponse">
            <div class="lotto-num">1</div>
            <div class="lotto-num">2</div>
            <div class="lotto-num">3</div>
            <div class="lotto-num">4</div>
            <div class="lotto-num">5</div>
            <div class="lotto-num">6</div>
            <div class="lotto-num bonus">7</div>
        </div>
    </div>
</template>
<script>
    import {AXIOS} from './http-common'
    export default {
        name: 'SearchLottoForm',
        data() {
            return {
                showResponse: false,
                errors: [],
                form: {
                    drawNo: ''
                }
            }
        },
        methods: {
            onSubmit (evt) {
                evt.preventDefault();
                AXIOS.get(`/draw/` + this.form.drawNo)
                    .then(response => {
                        // JSON responses are automatically parsed.
                        console.log(response.data)
                        this.showResponse = true
                    })
                    .catch(e => {
                        this.errors.push(e)
                    });
            }
        }
    }
</script>